<style scoped>
    .page {

    }
</style>
<template>
    <div class="page" style="text-align: left">
        <team-info :detail="detail"></team-info>
        <Divider></Divider>
        <member v-if="detail" :list="detail.members"></member>
        <Divider></Divider>
        <development v-if="detail" :list="detail.developments" :enter_team_id="detail.id"
                     @refresh="loadData"></development>
        <Divider></Divider>
        <linkman v-if="detail" :linkman="detail.linkman"></linkman>
        <Divider></Divider>
        <contentView v-if="detail" :detail="detail"></contentView>
    </div>
</template>

<script>
  import teamInfo from './view/info';
  import linkman from './view/linkman';
  import development from './view/development';
  import member from './view/member';
  import contentView from './view/content';
  import api from './api';

  export default {
    components: {
      teamInfo,
      development,
      linkman,
      contentView,
      member
    },
    data() {
      return {
        detail: null,
      };
    },
    mounted() {
      this.loadData();
    },
    methods: {
      loadData() {
        api.detail(res => {
          this.detail = res.data.data;
        }, () => {
          this.$router.push({
            name: 'contact'
          });
        });
      }
    }
  };
</script>
