<style scoped>
    .development {
        text-align: left;
    }

    .btn {
        color: #2d8cf0;
        margin-left: 5px;
        cursor: pointer;
        font-size: 0.8rem;
        margin-bottom: 5px;
        display: inline-block;
    }

    .hint {
        color: #888;
        margin-left: 5px;
        cursor: pointer;
        font-size: 0.8rem;
        margin-bottom: 5px;
        display: inline-block;
    }

    .btn:hover {
        color: #045cf0;

    }
</style>
<template>
    <div class="development">
        <h2>发展历程</h2>
        <Timeline style="padding:20px;" v-if="list">
            <TimelineItem v-for="(item, index) in list" v-bind:key="index">
                {{ item.date }}
                <span v-if="item.status===1" class="btn" @click="edit(item)">修改</span>
                <span v-if="item.status===0" class="hint">资料审核中</span>
                <br/>
                {{item.description}}
            </TimelineItem>
            <TimelineItem color="#BBB">
                <span class="btn" @click="edit(null)">添加</span>
            </TimelineItem>
        </Timeline>

        <Modal
                v-model="showDialog"
                title="编辑历程"
                :loading="loading"
        >
            <Form :model="selectItem" :label-width="80">
                <FormItem label="时间节点">
                    <DatePicker v-model="selectItem.date_time" format="yyyy年MM月dd日" type="date"
                                placeholder="请选择时间节点" style="width: 200px"></DatePicker>

                </FormItem>
                <FormItem label="文字介绍">
                    <Input v-model="selectItem.description" type="textarea" :rows="5" placeholder="请填写文字介绍"></Input>
                </FormItem>
            </Form>
            <div slot="footer">
                <Button @click="showDialog=false">取消</Button>
                <Button :loading="loading" type="info" @click="submitData">确认</Button>
            </div>
        </Modal>
    </div>
</template>

<script>
  import api from '../api';

  export default {
    props: ['list', 'enter_team_id'],
    data() {
      return {
        selectItem: {},
        showDialog: false,
        loading: false,
        formData: {},
      };
    },
    methods: {
      edit(item) {
        if (item != null) {
          this.selectItem = this.$formUtil.deepCopy(item);
        } else {
          this.selectItem = {};
        }
        this.showDialog = true;
      },
      submitData() {
        console.log('----');
        let data = this.selectItem;
        if (!this.$formUtil.isStrValid(data.date_time)) {
          this.$Message.error('请选择时间节点');
          return;
        }
        data.date_time = this.$formUtil.dataToStr(data.date_time);
        data.enter_team_id = this.enter_team_id;
        this.loading = true;
        api.developmentSave(data, res => {
          this.loading = false;
          this.showDialog = false;
          this.$emit('refresh');
        }, () => {
          this.loading = false;
        });
      }
    }
  };
</script>
